	module.exports = {"data":{"post":{"html":"<p>We just saw a news report about a children's museum exhibit on life in East Germany. I first visited East Berlin in 1981, and a lot has changed since then. The Wall is gone. The Palace of the Republic is gone. The DDR itself is gone. But my souvenir of my first visit remains: an original DDR beer opener, with the stamped Retail Sales Price (Einzelhandelsverkaufspreis = EVP) so you know it's authentic!</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3507455217\" title=\"View 'bottle_opener' on Flickr.com\"><img src=\"http://farm4.static.flickr.com/3302/3507455217_5afa1f186a.jpg\" alt=\"bottle_opener\" border=\"0\" width=\"500\" height=\"279\" /></a></p>\n<p>It's also still functional... buy me a beer, and I can prove it to you! </p>\n<p>For the record, at the time 15 DDR Pfennigs was worth about 3 BRD Pfennigs, or somewhat less than 1 US cent.</p>","fields":{"title":"East German Nostalgia","date":"06 May 2009","url":"https://papascott.de/2009-05-06-east-german-nostalgia/"}}},"pathContext":{"slug":"/2009-05-06-east-german-nostalgia/","prev":{"fields":{"title":"Whiney Expat Bloggers Meetup 2009","date":"28 Apr 2009","slug":"/2009-04-28-whiney-expat-bloggers-meetup-2009/"}},"next":{"fields":{"title":"Politically Active","date":"11 May 2009","slug":"/2009-05-11-politically-active/"}}}}

